import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-get-all-coupon-for-customer-by-max-price',
  templateUrl: './get-all-coupon-for-customer-by-max-price.component.html',
  styleUrls: ['./get-all-coupon-for-customer-by-max-price.component.css']
})
export class GetAllCouponForCustomerByMaxPriceComponent implements OnInit {
  public coupons:Coupon[]=new Array();
  public maxPrice: number;
  constructor(private myCustomerService:CustomerService) { }

  ngOnInit() {
  }
  getCouponsbyMaxPrice(){
  this.myCustomerService.getAllCouponForCustomerByMaxPrice(this.maxPrice).subscribe(coupons =>{
    this.coupons=coupons;
    console.log(coupons);
  });
}
}

