import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-get-all-coupon-for-customer',
  templateUrl: './get-all-coupon-for-customer.component.html',
  styleUrls: ['./get-all-coupon-for-customer.component.css']
})
export class GetAllCouponForCustomerComponent implements OnInit {
  public coupons: Coupon[] = new Array();
  constructor(private MyCustomerService: CustomerService) { }

  ngOnInit() {
    this.MyCustomerService.getAllCouponForCustomer().subscribe(coupons => {
      this.coupons = coupons;
      console.log(coupons);
    });
  }


  }
