import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-get-coupon-by-category',
  templateUrl: './get-coupon-by-category.component.html',
  styleUrls: ['./get-coupon-by-category.component.css']
})
export class GetCouponByCategoryComponent implements OnInit {
  public coupons:Coupon[]=new Array();
  public category: string;
  constructor(private myCompanySrvice:CompanyService) { }

  ngOnInit() {
  }
  getCouponbyCategory(){
  this.myCompanySrvice.getAllCouponForCompanyByCategory(this.category ).subscribe(coupons =>{
    this.coupons=coupons;
  });
}
}
