import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminService } from 'src/app/services/admin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {

  public customer:Customer = new Customer();

  constructor(private myAdminService:AdminService,private router: Router) { }

  ngOnInit() {

  }

  public addCustomer(){
    this.myAdminService.addNewCustomer(this.customer).subscribe(Response=>{
      alert("customer is added with id:" +Response.id), this.router.navigate(['admin']);},error=>{alert(error.error);
        this.router.navigate(['admin']);
    });
  }

}