import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-get-all-coupon-for-customer-by-category',
  templateUrl: './get-all-coupon-for-customer-by-category.component.html',
  styleUrls: ['./get-all-coupon-for-customer-by-category.component.css']
})
export class GetAllCouponForCustomerByCategoryComponent implements OnInit {
  public coupons:Coupon[]=new Array();
  public category: string;
  constructor(private myCustomerService:CustomerService) { }

  ngOnInit() {
  }
  getCouponbyCategory(){
  this.myCustomerService.getAllCouponForCustomerByCategory(this.category ).subscribe(coupons =>{
    this.coupons=coupons;
  });
}
}