import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company';
import { AdminService } from 'src/app/services/admin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css']
})
export class AddCompanyComponent implements OnInit {
  
  public company:Company = new Company();

  constructor(private myAdminService:AdminService,private router: Router) { }

  ngOnInit() {

  }

  public addCompany(){
    this.myAdminService.addNewCompany(this.company).subscribe(Response=>{
      alert("company is added with id:" +Response.id), this.router.navigate(['admin']);},error=>{alert(error.error);
      this.router.navigate(['admin']);
    } );
      
   
  }

}
