import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { Router } from '@angular/router';

@Component({
  selector: 'app-update-company',
  templateUrl: './update-company.component.html',
  styleUrls: ['./update-company.component.css']
})
export class UpdateCompanyComponent implements OnInit {

  public company:Company = new Company();

  constructor(private myAdminService:AdminService,private router: Router) { }

  ngOnInit() {
  }
  public updateCompany(){
    this.myAdminService.updateCompany(this.company).subscribe(Response=>{
      alert("the update is sucsess !!!company id:" +Response.id), this.router.navigate(['admin'])},error=>{alert(error.error)
        this.router.navigate(['admin']);
    });
 
  }
}
