import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-get-all-coupon',
  templateUrl: './get-all-coupon.component.html',
  styleUrls: ['./get-all-coupon.component.css']
})
export class GetAllCouponComponent implements OnInit {
  public coupons: Coupon[] = new Array();
  constructor(private MycompanyService: CompanyService) { }

  ngOnInit() {
    this.MycompanyService.getAllCouponForCompany().subscribe(coupons => {
      this.coupons = coupons;
      console.log(coupons);
    });
  }

  }