import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/services/company.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-update-coupon',
  templateUrl: './update-coupon.component.html',
  styleUrls: ['./update-coupon.component.css']
})
export class UpdateCouponComponent implements OnInit {

  public coupon:Coupon = new Coupon();

  constructor(private myCompanyService:CompanyService,private router: Router) { }

  ngOnInit() {
  }
  public updateCoupon(){
    this.myCompanyService.updateCoupon(this.coupon).subscribe(Response=>{
      alert("the update is sucsess !!!coupon id:" +Response.id), this.router.navigate(['company']);},error=>{alert(error.error);
        this.router.navigate(['company']);
    });
 
  }
}