import { Injectable } from '@angular/core';
import { HttpClient, } from '@angular/common/http';
import { Coupon } from '../models/coupon';
import { Company } from '../models/company';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  constructor(private client: HttpClient) { }
  public addNewCoupon(coupon:Coupon){
    return this.client.post<Coupon>("http://localhost:8080/Company/company/" + sessionStorage.token, coupon );
}
public updateCoupon(coupon:Coupon){
  return this.client.put<Coupon>("http://localhost:8080/Company/company/" + sessionStorage.token, coupon );
}
public deleteCoupon(id:number){
  return this.client.delete("http://localhost:8080/Company/company/"  + sessionStorage.token +"/"+ id ,{responseType: 'text'});
}
public getAllCouponForCompany(){
  return this.client.get<Coupon[]>("http://localhost:8080/Company/company/" + sessionStorage.token );
}
public  getAllCouponForCompanyByCategory(coupon:string){
  return this.client.get<Coupon[]>("http://localhost:8080/Company/company/category/" + sessionStorage.token +"/"+ coupon);
}
public  getAllCouponForCompanyByMaxPrice(maxPrice:Number){
  return this.client.get<Coupon[]>("http://localhost:8080/Company/company/maxPrice/" + sessionStorage.token +"/"+ maxPrice);
}
public  getCompanyDetails(){
  return this.client.get<Company>("http://localhost:8080/Company/company/details/" + sessionStorage.token);
}
}

