import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/services/company.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-coupon',
  templateUrl: './add-coupon.component.html',
  styleUrls: ['./add-coupon.component.css']
})
export class AddCouponComponent implements OnInit {
  public coupon:Coupon = new Coupon();

  constructor(private myCompanyService:CompanyService,private router: Router) { }


  ngOnInit() {
  }
  public addCoupon(){
    this.myCompanyService.addNewCoupon(this.coupon).subscribe(Response=>{
      alert("coupon is added with id:" +Response.id), this.router.navigate(['company']);},error=>{alert(error.error);
        this.router.navigate(['company']);
    });
  }

}

