import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminService } from 'src/app/services/admin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-update-customer',
  templateUrl: './update-customer.component.html',
  styleUrls: ['./update-customer.component.css']
})
export class UpdateCustomerComponent implements OnInit {
public customer:Customer =new Customer();
  constructor(private myAdminService:AdminService,private router: Router) { }

  ngOnInit() {
  }
  public updateCustomer(){
    this.myAdminService.updateCustomer(this.customer).subscribe(Response=>{
      alert("the update is sucsess !!!company id:" +Response.id), this.router.navigate(['admin']);},error=>{alert(error.error);
        this.router.navigate(['admin']);
    });
 
  }
}
