import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../models/coupon';
import { Customer } from '../models/customer';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  constructor(private client: HttpClient) { }
  public purchaseCoupon(coupon:Coupon){
    return this.client.post<Coupon>("http://localhost:8080/Customer/customer/" + sessionStorage.token, coupon );
}
public getAllCouponForCustomer(){
  return this.client.get<Coupon[]>("http://localhost:8080/Customer/customer/" + sessionStorage.token );
}
public getAllCouponForCustomerByCategory(coupon:string){
  return this.client.get<Coupon[]>("http://localhost:8080/Customer/customer/category/" + sessionStorage.token +"/"+ coupon);
}
public getAllCouponForCustomerByMaxPrice(maxPrice:Number){
  return this.client.get<Coupon[]>("http://localhost:8080/Customer/customer/maxprice/" + sessionStorage.token +"/"+ maxPrice);
}

public getCustomerDetails(){
  return this.client.get<Customer>("http://localhost:8080/Customer/customer/details/" + sessionStorage.token);
}
public GetAllCouponToBuyCustomer(){
  return this.client.get<Coupon[]>("http://localhost:8080/Customer/customer/couponsToBuy/" + sessionStorage.token);
}

}
