import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company';
import { AdminService } from 'src/app/services/admin.service';
import { Customer } from 'src/app/models/customer';
import { Response } from 'selenium-webdriver/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-delete-customer',
  templateUrl: './delete-customer.component.html',
  styleUrls: ['./delete-customer.component.css']
})
export class DeleteCustomerComponent implements OnInit {
  public customer: Customer = new Customer();

  constructor(private myAdminService: AdminService,private router: Router) { }

  ngOnInit() {
  }
  
  public deleteCustomer() {
    this.myAdminService.deleteCustomer(this.customer.id).subscribe(Response=>{
      alert("the delete is sucsess !!!"),this.router.navigate(['admin']);},error=>{alert(error.error);
        this.router.navigate(['admin']);
      }
    )};

}