import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { HttpResponse } from '@angular/common/http';
import { Response } from 'selenium-webdriver/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-delete-company',
  templateUrl: './delete-company.component.html',
  styleUrls: ['./delete-company.component.css']
})
export class DeleteCompanyComponent implements OnInit {
  public company: Company = new Company();

  constructor(private myAdminService: AdminService,private router: Router) { }

  ngOnInit() {
  }
  
  public deleteCompany() {
    this.myAdminService.deleteCompany(this.company.id).subscribe(Response=>{
      alert("company delete"), this.router.navigate(['admin'])},error=>{alert(error.error)
        this.router.navigate(['admin']);
      });
  }
}
