import { Component, OnInit } from '@angular/core';
import { CustomerService } from 'src/app/services/customer.service';
import { Coupon } from 'src/app/models/coupon';
import { Router } from '@angular/router';

@Component({
  selector: 'app-get-all-coupon-to-buy-customer',
  templateUrl: './get-all-coupon-to-buy-customer.component.html',
  styleUrls: ['./get-all-coupon-to-buy-customer.component.css']
})
export class GetAllCouponToBuyCustomerComponent implements OnInit {
public coupons:Coupon[]=new Array();
 
  constructor(private myCustomerService:CustomerService,private router: Router) { }

  ngOnInit() {
    
      this.myCustomerService.GetAllCouponToBuyCustomer().subscribe(coupons => {
        this.coupons = coupons;
        console.log(coupons);
      });
    }

    public buyMe(coupon:Coupon){
this.myCustomerService.purchaseCoupon(coupon).subscribe(coup=>{
alert("thank you!!!! ,you purchase coupon with coupon id:" +coup.id), this.router.navigate(['customer']);},error=>{alert(error.error);
  this.router.navigate(['customer']);
})
    }
  }


