import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-get-coupon-by-max-price',
  templateUrl: './get-coupon-by-max-price.component.html',
  styleUrls: ['./get-coupon-by-max-price.component.css']
})
export class GetCouponByMaxPriceComponent implements OnInit {
  public coupons:Coupon[]=new Array();
  public maxPrice: number;
  constructor(private myCompanySrvice:CompanyService) { }

  ngOnInit() {
  }
  getCouponbyMaxPrice(){
  this.myCompanySrvice.getAllCouponForCompanyByMaxPrice(this.maxPrice).subscribe(coupons =>{
    this.coupons=coupons;
    console.log(coupons);
  });
}
}
