import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/services/company.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-delete-coupon',
  templateUrl: './delete-coupon.component.html',
  styleUrls: ['./delete-coupon.component.css']
})
export class DeleteCouponComponent implements OnInit {
  public coupon: Coupon = new Coupon();

  constructor(private MyCompanyService: CompanyService,private router: Router) { }

  ngOnInit() {
  }
  
  public deleteCoupon() {
    this.MyCompanyService.deleteCoupon(this.coupon.id).subscribe(Response=>{
      alert("coupon was deleted"), this.router.navigate(['company']);},error=>{alert(error.error);
        this.router.navigate(['company']);
      }
      );
  }
}

