import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Company } from '../models/company';
import { Observable } from 'rxjs';
import { Customer } from '../models/customer';
import { Text } from '@angular/compiler/src/i18n/i18n_ast';
import { Response } from 'selenium-webdriver/http';

@Injectable({
  providedIn: 'root'
})
export class AdminService {
  addNewCoupon: any;

  constructor(private client: HttpClient) { }
 
  public addNewCompany(company:Company){
    return this.client.post<Company>("http://localhost:8080/admin/company/" + sessionStorage.token, company );
}
public updateCompany(company:Company){
  return this.client.put<Company>("http://localhost:8080/admin/company/" + sessionStorage.token , company );
}
public deleteCompany(id:number){
  return this.client.delete("http://localhost:8080/admin/company/"  + sessionStorage.token +"/"+ id ,{responseType: 'text'});
}

public getAllCompanies(): Observable<Company[]>{
  return this.client.get<Company[]>("http://localhost:8080/admin/companies/" + sessionStorage.token);
}
public getOneCompany(id:number){
  return this.client.get<Company>("http://localhost:8080/admin/company/" + sessionStorage.token +"/" +id );
}
public addNewCustomer(customer:Customer){
  return this.client.post<Customer>("http://localhost:8080/admin/customer/" + sessionStorage.token, customer );
}
public updateCustomer(customer:Customer){
  return this.client.put<Customer>("http://localhost:8080/admin/customer/" + sessionStorage.token, customer );
}
public deleteCustomer(id:number){
  return this.client.delete("http://localhost:8080/admin/customer/" + sessionStorage.token + "/" + id,{responseType: 'text'});
}
public getAllCustomers(): Observable<Customer[]>{
  return this.client.get<Customer[]>("http://localhost:8080/admin/customers/" + sessionStorage.token  );
}
public getOneCustomer(id:number){
  return this.client.get<Customer>("http://localhost:8080/admin/customer/" + sessionStorage.token+"/" +id );
}

}
